* {
  box-sizing: border-box;
}

:root {
  scroll-behavior: smooth;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
button,
input,
textarea {
  margin: 0;
  padding: 0;
  font: 16px Ubuntu, sans-serif;
  line-height: inherit;
  border-width: 0;
}

body {
  line-height: 1.5;
}

strong {
  font-weight: 700;
}

ul {
  list-style: none;
}
